/* src/App.css - fixes: slider legend size/position, hand-card outline, chart x-label, remove stats hand-summary */

:root{
  --bg:#f6fbfc;
  --card:#ffffff;
  --muted:#9aa6b2;
  --accent-start:#2bd0c8;
  --accent-end:#1f6ef8;
  --pill:#e9f6ff;
  --attention:#d94b4b;
}

*{box-sizing:border-box;font-family: 'Helvetica Neue', Arial, sans-serif}
body,html,#root{height:100%;margin:0;background:var(--bg);color:#0b1a23}

.app{max-width:920px;margin:0 auto;padding:16px 10px 80px}
.app-header{display:flex;flex-wrap:wrap;align-items:center;justify-content:space-between;gap:8px}
.title{font-size:34px;margin:0;font-weight:800;line-height:1.05;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}
.tab-buttons{display:flex;gap:6px}
.tab{padding:6px 12px;border-radius:16px;border:none;background:transparent;white-space:nowrap}
.tab.active{background:linear-gradient(90deg,var(--accent-start),var(--accent-end));color:#fff}

.main{margin-top:8px}
.top-row{display:flex;flex-wrap:wrap;gap:10px;align-items:center}
.avg-card{padding:12px;border-radius:12px;background:#fff;box-shadow:0 6px 16px rgba(0,0,0,.06)}
.avg-number{font-size:28px;font-weight:800}

.filter-row{display:flex;gap:6px;align-items:center;flex-wrap:wrap}
.filter-select,.reset-ui-btn{padding:6px 8px;border-radius:10px;border:1px solid #e6eef5;background:#fff}
.reset-ui-btn{font-size:13px}

.controls{margin-top:10px;display:flex;flex-direction:column;gap:8px}
.control-head{display:flex;align-items:center;gap:8px}
.control-head label{margin-right:6px;font-weight:600}
.legend{font-size:12px;color:var(--muted);margin-left:6px}

/* reduce legend font and place to the right of the label (as requested) */
.control-with-legend .legend{font-size:12px;white-space:nowrap}

/* hand cards - ensure outline not clipped and border visible */
.hand-cards{display:flex;gap:8px;overflow-x:auto;margin-top:8px;padding-bottom:6px}
.hand-card{flex:0 0 110px;padding:10px;border-radius:12px;background:#fff;text-align:center;box-shadow:0 6px 18px rgba(0,0,0,0.05);position:relative;overflow:visible}
.hand-card.selected{box-shadow:0 0 0 3px rgba(46,168,255,0.22);transform:translateY(-2px)}
.hand-emoji{font-size:30px}
.hand-sub{font-size:12px;color:var(--muted);margin-top:6px}

/* result pills */
.result-row{display:flex;gap:6px;margin-top:8px}
.pill{padding:6px 10px;border-radius:14px;background:var(--pill)}
.pill.active{background:linear-gradient(90deg,var(--accent-start),var(--accent-end));color:#fff}

/* save/back/buttons */
.save-btn{margin-top:8px;padding:10px;border-radius:14px;background:linear-gradient(90deg,var(--accent-start),var(--accent-end));color:#fff}
.back-btn{padding:6px 10px;border-radius:10px}

/* history table */
.history-table{width:100%;margin-top:12px;border-collapse:collapse;font-size:13px}
.history-table th,.history-table td{padding:8px;white-space:nowrap}
.small-btn{padding:6px 8px;border-radius:10px;border:none;background:#eef6fb;color:#1b6ea8;cursor:pointer;font-size:13px}

/* stats area - make chart taller so x labels fit and remove hand summaries */
.stats-section{margin-top:16px}
.chart-wrap{height:280px;overflow-x:auto;overflow-y:hidden}
.chart-inner{min-width:520px;height:280px;padding-bottom:46px}

/* remove hand-summary-cards on stats (user requested no duplication) */
.hand-summary-cards{display:none}

/* responsive tweaks */
@media (max-width:520px){
  .title{font-size:28px}
  .chart-inner{min-width:480px;height:260px}
  .chart-wrap{height:260px}
  .hand-card{flex:0 0 100px;padding:8px}
}
